import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";
import FeeInvoice from "./Fee_Form";
import { useState } from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";

import { blue, lightBlue, yellow } from "@material-ui/core/colors";
import {
    AppBar,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Toolbar,
  Typography,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
function createData(Class, Section, Arreas) {
  return { Class, Section, Arreas };
}

const rows = [
  createData("Frozen yoghurt", "one", 6.0),
  createData("Ice cream sandwich", "two", 9.0),
  createData("Eclair", "three", 16.0),
  createData("Cupcake", "four", 3.7),
  createData("Gingerbread", "five", 16.0),
];
const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText(yellow[900]),
    backgroundColor: blue[900],
    "&:hover": {
      backgroundColor: lightBlue[700],
    },
  },
  margin: {
    margin: theme.spacing(1),
  },
}))(Button);
const useStyles = makeStyles((theme) => ({
  appBar: {
    position: "relative",
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));
export default function Feemodal(props) {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const [opens, setOpens] = useState(false);
  const { text, row } = props;

  return (
    <>
      <div style={{ marginTop: 5 }}>
        <ColorButton
          variant="contained"
          color="primary"
          className={classes.margin}
          onClick={() => {
            setOpen(true);
          }}
        >
          {text}
        </ColorButton>

        <Dialog
          id={JSON.stringify(row)}
          key={JSON.stringify(row)}
          open={open}
          // TransitionComponent={Transition}
          fullWidth={true}
          maxWidth="md"
          onClose={open}
          keepMounted
          aria-labelledby="diologe"
          aria-describedby="diologee"
        >
          <AppBar className={classes.appBar}>
            <Toolbar>
              <Typography variant="h6" className={classes.title}>
                Take Student Fee
              </Typography>
              <IconButton
                edge="start"
                color="inherit"
                onClick={() => setOpen(false)}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
            </Toolbar>
          </AppBar>
          <DialogContent>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Class</TableCell>
                  <TableCell align="start">Sectoin</TableCell>
                  <TableCell align="start">Arreas</TableCell>
                </TableRow>
              </TableHead>

              <TableBody>
                {rows.map((ele, ind) => {
                  return (
                    <TableRow key={ind}>
                      <TableCell component="th" scope="row">
                        {ele.Class}
                      </TableCell>
                      <TableCell align="start">{ele.Section}</TableCell>
                      <TableCell>
                        <Button
                          onClick={() => {
                            setOpens(true);
                          }}
                          variant="contained"
                        >
                          {ele.Arreas}
                        </Button>
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
            {/* <FeeInvoice setOpen={setOpen} handleClose={open} row={{ ...row }} /> */}
          </DialogContent>
        </Dialog>
      </div>

      <Dialog
        id={JSON.stringify(row)}
        key={JSON.stringify(row)}
        open={opens}
        // TransitionComponent={Transition}
        fullWidth={true}
        maxWidth="md"
        keepMounted
        // onClose={handleClose}
        aria-labelledby="diologe"
        aria-describedby="diologee"
      >
        <DialogTitle id="diologee">{"Take Student Fee"}</DialogTitle>
        <DialogContent>
          <FeeInvoice setOpen={setOpens} handleClose={open} row={{ ...row }} />
        </DialogContent>
      </Dialog>
    </>
  );
}
