import React from "react";
import * as Yup from "yup";
import {
  Box,
  Card,
  Typography,
  MenuItem,
  Button,
  FormControl,
  Select,
  InputLabel,
} from "@material-ui/core";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Formik } from "formik";
import { useState, useEffect } from "react";
import axios from "axios";
import { Section } from "../HelperFunction";
import Input from "../InputFeild/Input";
import { green, yellow } from "@material-ui/core/colors";
import Feemodal from "./Feemodal";
import Fee_Arreas from "./Fee_Arreas";
import {customAxios} from "../apis/Axios";

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText(yellow[900]),
    backgroundColor: yellow[900],
    "&:hover": {
      backgroundColor: yellow[700],
    },
  },
  margin: {
    margin: theme.spacing(1),
  },
}))(Button);

const useStyles = makeStyles({
  table: {
    minWidth: 700,
    // marginTop:10
  },

  feild: {
    width: "20%",
    marginLeft: 5,
  },
});

const Attendence_List = () => {
  const classes = useStyles();
  const [classRoom, setClassRoom] = useState([]);
  const [section, setSection] = useState([]);
  const [Data, setData] = useState([]);
  const data = Object.values(Data);
 console.log(data)
  // const handleClickOpen = () => {
  //   setOpen(true);
  // };

  // const handleClose = () => {
  //   setOpen(false);
  // };
  useEffect(() => {
    const fun = Section("class/api");
    fun.then((res) => {
      setClassRoom(res);
    });
    const fun1 = Section("class/api/section");
    fun1.then((res) => {
      setSection(res);
    });
  }, []);

  if (!Data) return null;
  return (
    <>
      <Formik
        initialValues={{
          name: "",
          classid: "",
          sectionid: "",
          // submit: null,
        }}
        validationSchema={Yup.object().shape({})}
        onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
          // try {
          // alert(JSON.stringify(values), null);
          // Call API to store step data in server session

          const data = { ...values };
          // console.log(data);
          // await new Promise((r) => setTimeout(r, 500));
          await customAxios
            .getCalls("fee/fee/dataget", {
              params: {
                ...data,
              },
            })
            .then((res) => {
              setData(res);
            });
        }}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          setFieldValue,
          setFieldTouched,
          touched,
          values,
        }) => (
          <form onSubmit={handleSubmit}>
            <Card sx={{ p: 3 }}>
              <Typography color="textPrimary" variant="h6">
                Select Fee Info And Detail
              </Typography>
              <Box sx={{ mt: 2 }}>
                <Input
                  autoFocus
                  error={Boolean(touched.name && errors.name)}
                  helperText={touched.name && errors.name}
                  id="name"
                  label="Name"
                  variant="outlined"
                  value={values.name}
                  onBlur={handleBlur}
                  onChange={handleChange}
                />
                <FormControl className={classes.feild}>
                  <InputLabel id="classid">Department/Class</InputLabel>

                  <Select
                    fullWidth
                    labelId="classid"
                    value={values.classid}
                    onChange={handleChange}
                    id="classid"
                    label="Department/Class"
                    name="classid"
                  >
                    {classRoom.map((option) => (
                      <MenuItem key={option.id} value={option.id}>
                        {option.className}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <FormControl className={classes.feild}>
                  <InputLabel id="sectionid">Section</InputLabel>

                  <Select
                    fullWidth
                    labelId="sectionid"
                    value={values.sectionid}
                    onChange={handleChange}
                    id="sectionid"
                    label="Section"
                    name="sectionid"
                  >
                    {section.map((option) => (
                      <MenuItem key={option.id} value={option.id}>
                        {option.name}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                <Button
                  sx={{ mt: 1, mr: 2, ml: 2 }}
                  color="primary"
                  disabled={isSubmitting}
                  type="submit"
                  variant="contained"
                >
                  Select
                </Button>
              </Box>
            </Card>
          </form>
        )}
      </Formik>

      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Roll No</StyledTableCell>
              <StyledTableCell>Name</StyledTableCell>
              <StyledTableCell>Class</StyledTableCell>
              <StyledTableCell>Section</StyledTableCell>
              <StyledTableCell>TotalFee</StyledTableCell>
              <StyledTableCell>Paid</StyledTableCell>
              <StyledTableCell>Discount</StyledTableCell>

              <StyledTableCell>Balance</StyledTableCell>

              <StyledTableCell></StyledTableCell>
              <StyledTableCell>Action</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.length > 0 &&
              data.map((row) => (
                <StyledTableRow key={row.id}>
                  <StyledTableCell component="th" scope="row">
                    {row.rollno}
                  </StyledTableCell>
                  <StyledTableCell align="start">{`${row.name}`}</StyledTableCell>
                  <StyledTableCell align="start">
                    {row.className}
                  </StyledTableCell>
                  <StyledTableCell align="start">{row.secname}</StyledTableCell>
                  <StyledTableCell align="start">
                    {row.TotalFee}
                  </StyledTableCell>
                  <StyledTableCell align="start">{`${
                    row.Paid ? row.Paid : "Unpaid"
                  }`}</StyledTableCell>
                  <StyledTableCell align="start">{`${
                    row.discount ? row.discount : "0"
                  }`}</StyledTableCell>

                  <StyledTableCell align="start">{`${
                    row.Balance != 0 ? row.Balance : "Paid"
                  }`}</StyledTableCell>
                  <StyledTableCell align="start">{row.attend}</StyledTableCell>
                  <StyledTableCell>
                    <Feemodal
                      row={row}
                      // handleClickOpen={handleClickOpen}
                      // handleClose={handleClose}
                      // open={open}
                      text="$ Pay Fee"
                    />
                    <Fee_Arreas row={row} text="$ Arreas" />
                  </StyledTableCell>
                </StyledTableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};
export default Attendence_List;
