import React, { useState, useEffect } from "react";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import { MobileDatePicker } from "@material-ui/lab";
import axios from "axios";
import {
  Box,
  Card,
  TextField,
  Typography,
  MenuItem,
  RadioGroup,
  FormControlLabel,
  Radio,
  FormLabel,
  Stack,
  FormControl,
  Select,
  InputLabel,
  Button,
} from "@material-ui/core";
import { useLocation } from "react-router-dom";
import toast from "react-hot-toast";

import { Field, Formik } from "formik";
import Save from "../Buttons/Save";
import ResetButton from "../Buttons/ResetButton";
import { Section } from "../HelperFunction";
// import Input from "../InputFeild/Input";
import Design from "../Design";
import {customAxios} from "../apis/Axios";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
      margin: "17px",
    },
  },
  typography: {
    padding: "17px",
  },
  head: {
    height: "160vh",
    paddingTop: 10,
    background: "none",
    boxShadow: "none",
  },
}));

const Paymethod = [
  {
    value: "0",
    label: "cheque",
  },
  {
    value: "1",
    label: "Cash",
  },
  {
    value: "2",
    label: "Net Banking",
  },
];

const Fee = [
  {
    value: "0",
    label: "Admission Fee",
  },
  {
    value: "01",
    label: "Semester Fee",
  },
  {
    value: "1",
    label: "Tuition Fee",
  },
  {
    value: "2",
    label: "Annual Fee",
  },
  {
    value: "3",
    label: "Exams Fee",
  },
  {
    value: "4",
    label: "Transport Fee",
  },

];
const Department = [
  {
    value: "0",
    label: "metric",
  },
  {
    value: "1",
    label: "two",
  },
  {
    value: "2",
    label: "one",
  },
];
const sections = [
  {
    value: "0",
    label: "Section A",
  },
  {
    value: "1",
    label: "B",
  },
  {
    value: "2",
    label: "Section C",
  },
];
const Tax_Rate = 10000;
export default function FeeInvoice(props) {
  const { row, setOpen, handleClose } = props;
  const classes = useStyles();
  console.log("=>", row);
 
  return (
    <>
      <Card className={classes.head}>
        <Formik
          enableReinitialize
          initialValues={{
            rollno: row.rollno || "",
            name: row.name || "",
            classid: row.className,
            secname: row.secname,
            class: row.classid || "",
            sectId: row.sectId || "",
            feetype: "",
            payduration: "",
            date: new Date(),
            totalfee: row.TotalFee || "",
            balance: row.Balance != 0 ? row.Balance : "paid" || "",
            email:row.email||"",
            F_phone:row.F_phone,
            paid: "",
            paymethod: "",
            detail: ``,
            status: 1,
          }}
          validationSchema={Yup.object().shape({
            rollno: Yup.number().required("Rollno is required"),
            name: Yup.string().max(255).required("Password is required"),
            paid: Yup.number().required("Password is required"),
            totalfee: Yup.number().required("Password is required"),
            paid: Yup.number().max(
              Yup.ref("balance"),
              "Paid Amount Must Be less Then Balance Amount"
            ),
          })}
          onSubmit={async (values) => {
            await new Promise((r) => setTimeout(r, 500));
            await customAxios.postCall("fee/fee/collecetion", {
              ...values,
            });
            // alert(JSON.stringify(values, null, 2));
            toast.success("pay success full");
            setOpen(false);
            // setOpen(false);
          }}
        >
          {({
            errors,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting,
            setFieldValue,
            setFieldTouched,
            dirty,
            touched,
            values,
            handleReset,
          }) => (
            <div>
              {/* <Design text="Student Fee" dashText="Add Student Fee" /> */}
              <Box style={{ paddingLeft: 20 }} color="black">
                <form noValidate onSubmit={handleSubmit}>
                  <div>
                    <TextField
                      autoFocus
                      autoComplete="off"
                      disabled
                      id="rollno"
                      label="Roll No"
                      style={{ margin: 8, width: "95%" }}
                      variant="outlined"
                      type="number"
                      error={Boolean(touched.rollno && errors.rollno)}
                      helperText={touched.rollno && errors.rollno}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.rollno}
                    />
                    <TextField
                      id="name"
                      label="Student Name"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      disabled
                      error={Boolean(touched.name && errors.name)}
                      helperText={touched.name && errors.name}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      margin="normal"
                      value={values.name}
                      variant="outlined"
                    />
                    {/* <FormControl style={{ margin: 8, width: "95%" }}>
                      <InputLabel id="classid">Department/Class</InputLabel>

                      <Select
                        fullWidth
                        labelId="classid"
                        value={values.classid}
                        disabled
                        onChange={handleChange} 
                      
                    //     id="classid"
                    //     label="Department/Class"
                    //     name="classid"
                    //   >
                    //     {classRoom.map((option) => (
                    //       <MenuItem key={option.id} value={option.id}>
                    //         {option.className}
                    //       </MenuItem>
                    //     ))}
                    //   </Select>
                    // </FormControl>*/}
                    <TextField
                      id="name"
                      label="Department/Class"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      disabled
                      error={Boolean(touched.classid && errors.classid)}
                      helperText={touched.classid && errors.classid}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      margin="normal"
                      value={values.classid}
                      variant="outlined"
                    />
                    <TextField
                      id="name"
                      label="Section"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      disabled
                      error={Boolean(touched.secname && errors.secname)}
                      helperText={touched.secname && errors.secname}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      margin="normal"
                      value={values.secname}
                      variant="outlined"
                    />
                    {/* <FormControl style={{ margin: 8, width: "95%" }}>
                      <InputLabel id="secname">Section</InputLabel>

                      <Select
                        fullWidth
                        labelId="secname"
                        value={values.secname}
                        onChange={handleChange}
                        id="secname"
                        // disabled
                        label="Section"
                        name="secname"
                      >
                        {section.map((option) => (
                          <MenuItem key={option.id} value={option.id}>
                            {option.name}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl> */}
                    <FormControl style={{ margin: 8, width: "95%" }}>
                      <InputLabel id="feetype">Fee Type</InputLabel>

                      <Select
                        fullWidth
                        labelId="fee"
                        value={values.feetype}
                        onChange={handleChange}
                        id="feetype"
                        label="fee Type"
                        name="feetype"
                      >
                        {Fee.map((option) => (
                          <MenuItem key={option.value} value={option.label}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                    <FormControl
                      style={{ paddingLeft: 10 }}
                      component="fieldset"
                    >
                      <FormLabel component="legend">Payment Duration</FormLabel>
                      <RadioGroup
                        row
                        aria-label="payduration"
                        name="payduration"
                        id="payduration"
                        value={values.payduration}
                        onChange={handleChange}
                      >
                        <FormControlLabel
                          value="monthly"
                          control={<Radio />}
                          label="monthly"
                        />
                        <FormControlLabel
                          value="session"
                          control={<Radio />}
                          label="Session"
                        />
                        <FormControlLabel
                          value="yearly"
                          control={<Radio />}
                          label="Yearly"
                        />
                      </RadioGroup>
                    </FormControl>
                    <FormControl style={{ margin: 8, width: "95%" }}>
                      <MobileDatePicker
                        // sx={{ margin: 8, width: "20%" }}
                        fullwidth
                        label="Date"
                        onAccept={() => setFieldTouched("date")}
                        onChange={(date) =>
                          setFieldValue("date", date.toLocaleDateString())
                        }
                        // inputFormat="yyyy-dd-MM"
                        inputFormat=" yyyy/MM/dd"
                        onClose={() => setFieldTouched("date")}
                        renderInput={(inputProps) => (
                          <TextField variant="outlined" {...inputProps} />
                        )}
                        value={values.date}
                      />
                    </FormControl>
                    <TextField
                      error={Boolean(touched.totalfee && errors.totalfee)}
                      helperText={touched.totalfee && errors.totalfee}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      id="totalfee"
                      label="Total Fee"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      disabled
                      margin="normal"
                      variant="outlined"
                      value={values.totalfee}
                    />
                    <TextField
                      error={Boolean(touched.balance && errors.balance)}
                      helperText={touched.balance && errors.balance}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      id="paymentrefno"
                      label="Balance"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      margin="normal"
                      variant="outlined"
                      value={values.balance}
                      disabled
                    />
                    <TextField
                      error={Boolean(touched.paid && errors.paid)}
                      helperText={touched.paid && errors.paid}
                      // onBlur={handleBlur}
                      // onBlur={(e) => {
                      //   console.log(e.target.value);
                      //   paidBalance(e.target.value);
                      //   handleBlur(e);
                      // }}
                      onChange={handleChange}
                      // onChange={(e) => {
                      //   paidBalance(values.balance, e);
                      //   handleChange
                      // }}
                      id="paid"
                      label="Amount"
                      type="number"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      margin="normal"
                      name="paid"
                      variant="outlined"
                      value={values.paid}
                    />
                    <FormControl style={{ margin: 8, width: "95%" }}>
                      <InputLabel id="paymethod">pay Method</InputLabel>

                      <Select
                        fullWidth
                        labelId="paymethod"
                        value={values.paymethod}
                        onChange={handleChange}
                        id="paymethod"
                        label="pay Method"
                        name="paymethod"
                      >
                        {Paymethod.map((option) => (
                          <MenuItem key={option.value} value={option.label}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl>
                    <TextField
                      error={Boolean(touched.detail && errors.detail)}
                      helperText={touched.detail && errors.detail}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      id="detail"
                      label="Detail"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      multiline
                      rows={4}
                      margin="normal"
                      variant="outlined"
                      value={values.detail}
                    />{" "}
                  </div>
                  <Save
                    // handleClose={handleClose}
                    text="Pay"
                    variant="contained"
                    size="medium"
                    color="success"
                    // disableElevation
                    type="submit"
                    disabled={isSubmitting}
                  />
                  {/* <button type="submit" disabled={isSubmitting}>
                    Submit
                  </button> */}
                  <ResetButton
                    text="Reset"
                    variant="contained"
                    size="medium"
                    color="primary"
                    type="button"
                    onClick={handleReset}
                    disabled={!dirty || isSubmitting}
                    // disableElevation
                  />{" "}
                  <ResetButton
                    text="Close"
                    variant="contained"
                    size="medium"
                    color="error"
                    type="button"
                    onClick={() => setOpen(false)}
                    // disableElevation
                  />
                </form>
              </Box>
            </div>
          )}
        </Formik>
        <Typography style={{ marginTop: 20 }}>
          Â© Copyrights <b>akkhor</b> 2019. All rights reserved. Designed by
          PsdBosS
        </Typography>
      </Card>
    </>
  );
}
