import { TextField } from '@material-ui/core';
import React from 'react'

const Input = (props) => {
    const { autoFocus=false, error, helperText,id,label,variant,onBlur,value,onChange,autocomplete } = props;
    // console.log("vv", props);
  return (
    <TextField
    autoComplete='of'
      autocomplete={autocomplete}
      autoFocus={autoFocus}
      error={error}
      helperText={helperText}
      id={id}
      label={label}
      variant={variant}
      value={value}
      onBlur={onBlur}
      onChange={onChange}
    />
  );
};

export default Input