import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";
import FeeInvoice from "./Fee_Form";
import { useState } from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";

import { blue, lightBlue, yellow } from "@material-ui/core/colors";


const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText(yellow[900]),
    backgroundColor: blue[900],
    "&:hover": {
      backgroundColor: lightBlue[700],
    },
  },
  margin: {
    margin: theme.spacing(1),
  },
}))(Button);
const useStyles = makeStyles({

});
export default function Feemodal(props) {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const { handleClose, handleClickOpen, text, row } = props;

  return (
    <div style={{marginTop:5}}>
      
      <ColorButton
        variant="contained"
        color="primary"
        className={classes.margin}
        onClick={() => {
          setOpen(true);
        }}
      >
        {text}
      </ColorButton>
      
      <Dialog
        id={JSON.stringify(row)}
        key={JSON.stringify(row)}
        open={open}
        // TransitionComponent={Transition}
        fullWidth={true}
        maxWidth="md"
        keepMounted
        // onClose={handleClose}
        aria-labelledby="diologe"
        aria-describedby="diologee"
      >
        <DialogTitle id="diologee">{"Take Student Fee"}</DialogTitle>
        <DialogContent>
 
          <FeeInvoice setOpen={setOpen} handleClose={open} row={{ ...row }} />
        </DialogContent>
      </Dialog>
    </div>
  );
}
